"use client";
import { useState, useEffect } from "react";
import { BiSearch, BiCheck, BiX } from "react-icons/bi";
import { FiEye } from "react-icons/fi";
import useAdminStore from "@/store/adminStore";
import { adminService } from "@/services/adminService";
import toast from "react-hot-toast";

// Get store state directly for debugging
const getStoreState = () => useAdminStore.getState();

export default function FundraiserRequestTable() {
  const { campaigns, isLoading, getAllCampaigns, reviewCampaign } = useAdminStore();
  const [searchTerm, setSearchTerm] = useState("");
  const [entriesPerPage, setEntriesPerPage] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);

  useEffect(() => {
    const fetchRequests = async () => {
      try {
        console.log("=== Fetching fundraiser requests ===");
        console.log("Request params:", { status: 'pending', limit: 100 });
        
        // Fetch all campaigns first, then filter for pending/draft on frontend
        // This ensures we catch both 'pending' and 'draft' status campaigns
        // (Some old campaigns might have 'draft' status)
        const response = await getAllCampaigns({ limit: 100 });
        console.log("API Response:", response);
        console.log("Response data:", response?.data);
        console.log("Response campaigns:", response?.campaigns);
        
        // Wait a bit for store to update
        await new Promise(resolve => setTimeout(resolve, 200));
        
        const storeState = useAdminStore.getState();
        const currentCampaigns = storeState.campaigns || [];
        
        console.log("=== Store State ===");
        console.log("Campaigns in store:", currentCampaigns.length);
        console.log("All campaigns:", currentCampaigns);
        console.log("Campaigns with status:", currentCampaigns.map(c => ({ id: c._id, title: c.title, status: c.status })));
        
        // Filter for pending and draft campaigns (both need admin approval)
        const pendingCampaigns = currentCampaigns.filter(c => 
          c.status === 'pending' || c.status === 'draft'
        );
        console.log("Filtered pending/draft campaigns:", pendingCampaigns.length);
        console.log("Pending campaigns:", pendingCampaigns);
        console.log("All campaign statuses:", [...new Set(currentCampaigns.map(c => c.status))]);
        
        if (currentCampaigns.length === 0) {
          console.warn("âš ï¸ No campaigns in store! Check if API returned data.");
        }
        
        if (pendingCampaigns.length === 0 && currentCampaigns.length > 0) {
          console.warn("âš ï¸ No pending campaigns found. Available statuses:", [...new Set(currentCampaigns.map(c => c.status))]);
        }
      } catch (error) {
        console.error("âŒ Failed to load fundraiser requests:", error);
        console.error("Error response:", error.response);
        console.error("Error details:", error.response?.data);
        toast.error(error.response?.data?.message || "Failed to load requests");
      }
    };
    fetchRequests();
  }, [getAllCampaigns]);

  // Debug: Log campaigns when they change
  useEffect(() => {
    if (campaigns.length > 0) {
      console.log("Fundraiser requests loaded:", campaigns.length);
      console.log("Sample campaign:", campaigns[0]);
    } else if (!isLoading) {
      console.log("No fundraiser requests found");
    }
  }, [campaigns, isLoading]);

  const handleRefresh = async () => {
    try {
      // Fetch all campaigns, then filter for pending/draft on frontend
      await getAllCampaigns({ limit: 100 });
      toast.success("Data refreshed");
    } catch (error) {
      console.error("Refresh error:", error);
      toast.error("Failed to refresh data");
    }
  };

  const handleApprove = async (campaignId) => {
    try {
      console.log("Approving campaign:", campaignId);
      
      // Use adminService directly to ensure correct endpoint
      const response = await adminService.approveCampaign(campaignId);
      console.log("Approve response:", response);
      
      toast.success("Fundraiser approved successfully!");
      
      // Refresh data after approval - fetch all campaigns and filter
      setTimeout(() => {
        getAllCampaigns({ limit: 100 });
      }, 500);
    } catch (error) {
      console.error("Approve error:", error);
      console.error("Error details:", error.response || error);
      const errorMessage = error.response?.data?.message || error.message || "Failed to approve fundraiser";
      toast.error(errorMessage);
    }
  };

  const handleReject = async (campaignId) => {
    try {
      console.log("Rejecting campaign:", campaignId);
      // Backend endpoint is 'reject', not 'rejected'
      const response = await reviewCampaign(campaignId, 'reject');
      console.log("Reject response:", response);
      
      toast.success("Fundraiser rejected");
      
      // Refresh data after rejection - fetch all campaigns and filter
      setTimeout(() => {
        getAllCampaigns({ limit: 100 });
      }, 500);
    } catch (error) {
      console.error("Reject error:", error);
      console.error("Error details:", error.response || error);
      const errorMessage = error.response?.data?.message || error.message || "Failed to reject fundraiser";
      toast.error(errorMessage);
    }
  };

  // Filter campaigns - show pending and draft status
  // Note: Campaigns should be 'pending' but sometimes default to 'draft'
  // So we show both to catch all pending requests
  console.log("Rendering: campaigns array length:", campaigns?.length || 0);
  console.log("Rendering: campaigns:", campaigns);
  
  const pendingCampaigns = (campaigns || []).filter(campaign => {
    const status = campaign?.status;
    // Show both 'pending' and 'draft' as they both need admin approval
    const isPending = status === 'pending' || status === 'draft';
    if (campaign && !isPending) {
      console.log(`Campaign ${campaign._id} (${campaign.title}) has status: ${status} - skipping`);
    }
    return isPending;
  });

  console.log("Rendering: pendingCampaigns count:", pendingCampaigns.length);

  const filteredRequests = pendingCampaigns.filter(campaign =>
    campaign.title?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    campaign.fundraiser?.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    campaign.fundraiser?.email?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    campaign.beneficiary?.name?.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const indexOfLastEntry = currentPage * entriesPerPage;
  const indexOfFirstEntry = indexOfLastEntry - entriesPerPage;
  const currentEntries = filteredRequests.slice(indexOfFirstEntry, indexOfLastEntry);
  const totalPages = Math.ceil(filteredRequests.length / entriesPerPage);

  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  return (
    <>
      <div className="mb-8">
        <h1 className="text-4xl font-bold text-gray-900 mb-2">See Data</h1>
      </div>

      <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-6">
        <div className="flex items-center justify-between mb-6 flex-wrap gap-4">
          <div className="flex items-center gap-2">
            <select
              value={entriesPerPage}
              onChange={(e) => {
                setEntriesPerPage(Number(e.target.value));
                setCurrentPage(1);
              }}
              className="border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value={10}>10</option>
              <option value={25}>25</option>
              <option value={50}>50</option>
              <option value={100}>100</option>
            </select>
            <span className="text-sm text-green-600 font-medium">entries per page</span>
          </div>

          <div className="flex items-center gap-3">
            <button
              onClick={handleRefresh}
              className="px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition text-sm font-medium flex items-center gap-2"
              title="Refresh data"
            >
              <span>ðŸ”„</span> Refresh
            </button>
            <div className="relative">
              <input
                type="text"
                placeholder="Search..."
                value={searchTerm}
                onChange={(e) => {
                  setSearchTerm(e.target.value);
                  setCurrentPage(1);
                }}
                className="border border-gray-300 rounded-lg pl-4 pr-10 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 w-64"
              />
              <BiSearch className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={20} />
            </div>
          </div>
        </div>

        {isLoading ? (
          <div className="text-center py-12">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-green-500 mx-auto"></div>
            <p className="text-gray-600 mt-4">Loading requests...</p>
          </div>
        ) : (
          <>
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="bg-gradient-to-r from-green-50 to-blue-50 border-b-2 border-green-200">
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                      Campaign Title
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                      Fundraiser
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                      Contact
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                      Goal Amount
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                      Category
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                      Date
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                      Actions
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {currentEntries.length > 0 ? (
                    currentEntries.map((campaign, index) => (
                      <tr
                        key={campaign._id}
                        className={`border-b border-gray-100 hover:bg-gray-50 transition-colors ${
                          index % 2 === 0 ? 'bg-white' : 'bg-gray-50/30'
                        }`}
                      >
                        <td className="px-4 py-4 text-sm font-medium text-gray-900">
                          <div className="line-clamp-2">{campaign.title}</div>
                        </td>
                        <td className="px-4 py-4 text-sm">
                          <div className="text-gray-900">{campaign.fundraiser?.name || 'N/A'}</div>
                          <div className="text-xs text-gray-600">{campaign.fundraiser?.email}</div>
                        </td>
                        <td className="px-4 py-4 text-sm text-gray-700">
                          <div className="space-y-1">
                            {campaign.beneficiary?.contact?.phone ? (
                              <div className="flex items-center gap-1">
                                <span className="text-gray-500">ðŸ“±</span>
                                <span>{campaign.beneficiary.contact.phone}</span>
                              </div>
                            ) : campaign.fundraiser?.phone ? (
                              <div className="flex items-center gap-1">
                                <span className="text-gray-500">ðŸ“±</span>
                                <span>{campaign.fundraiser.phone}</span>
                              </div>
                            ) : null}
                            {campaign.beneficiary?.contact?.email ? (
                              <div className="flex items-center gap-1 text-xs text-gray-500">
                                <span>âœ‰ï¸</span>
                                <span>{campaign.beneficiary.contact.email}</span>
                              </div>
                            ) : null}
                            {!campaign.beneficiary?.contact?.phone && !campaign.fundraiser?.phone && (
                              <span className="text-gray-400">N/A</span>
                            )}
                          </div>
                        </td>
                        <td className="px-4 py-4 text-sm font-medium text-gray-900">
                          â‚¹{campaign.goalAmount?.toLocaleString() || 0}
                        </td>
                        <td className="px-4 py-4">
                          <span className="px-2 py-1 rounded-full text-xs font-medium bg-blue-100 text-blue-700">
                            {campaign.category}
                          </span>
                        </td>
                        <td className="px-4 py-4 text-sm text-gray-700">
                          {new Date(campaign.createdAt).toLocaleDateString()}
                        </td>
                        <td className="px-4 py-4">
                          <div className="flex gap-2">
                            <button
                              onClick={() => handleApprove(campaign._id)}
                              className="flex items-center gap-1 px-3 py-1.5 bg-green-600 text-white text-xs font-medium rounded-lg hover:bg-green-700 transition-colors"
                              title="Approve Campaign"
                            >
                              <BiCheck size={18} />
                              Approve
                            </button>
                            <button
                              onClick={() => handleReject(campaign._id)}
                              className="flex items-center gap-1 px-3 py-1.5 bg-red-600 text-white text-xs font-medium rounded-lg hover:bg-red-700 transition-colors"
                              title="Reject Campaign"
                            >
                              <BiX size={18} />
                              Reject
                            </button>
                            <a 
                              href={`/campaigns/${campaign._id}`}
                              target="_blank"
                              className="flex items-center gap-1 px-3 py-1.5 bg-blue-600 text-white text-xs font-medium rounded-lg hover:bg-blue-700 transition-colors"
                              title="View Details"
                            >
                              <FiEye size={14} />
                              View
                            </a>
                          </div>
                        </td>
                      </tr>
                    ))
                  ) : (
                    <tr>
                      <td colSpan="7" className="px-4 py-12 text-center text-gray-500">
                        <div className="text-4xl mb-2">ðŸ“‹</div>
                        {isLoading ? (
                          'Loading requests...'
                        ) : (
                          <div>
                            <p className="mb-2">No fundraiser requests found</p>
                            {campaigns.length > 0 && (
                              <div className="text-xs text-gray-400 mt-2">
                                <p>Total campaigns in store: {campaigns.length}</p>
                                <p>Pending campaigns: {pendingCampaigns.length}</p>
                                <p>Available statuses: {[...new Set(campaigns.map(c => c.status))].join(', ')}</p>
                                <p className="mt-2 text-blue-500 cursor-pointer" onClick={() => console.log("All campaigns:", campaigns)}>
                                  Click to see all campaigns in console
                                </p>
                              </div>
                            )}
                          </div>
                        )}
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>

            <div className="flex items-center justify-between mt-6">
              <div className="text-sm text-gray-600">
                Showing {indexOfFirstEntry + 1} to {Math.min(indexOfLastEntry, filteredRequests.length)} of {filteredRequests.length} entries
              </div>

              <div className="flex items-center gap-2">
                {Array.from({ length: totalPages }, (_, i) => i + 1).map((page) => (
                  <button
                    key={page}
                    onClick={() => paginate(page)}
                    className={`px-3 py-1 rounded-md text-sm font-medium transition-colors ${
                      currentPage === page
                        ? 'bg-gray-200 text-gray-900'
                        : 'text-gray-600 hover:bg-gray-100'
                    }`}
                  >
                    {page}
                  </button>
                ))}
              </div>
            </div>
          </>
        )}
      </div>
    </>
  );
}



